<template>
	<b-list-group-item button class="d-flex align-items-center justify-content-between user-select-none" @click="clicked">
		<div class="available_date d-flex align-items-center">
			<BIcon icon="calendar-check" width="1rem" scale="1.5" variant="success" v-if="intervals.length" />
			<BIcon icon="calendar-x" width="1rem" scale="1.5" class="text-muted" v-else />
			<span class="ml-3">{{ displayDate }}</span>
		</div>
		<div class="available_time d-flex align-items-center text-muted">
			<BIcon icon="arrow-repeat" width="1rem" scale="1.2" class="text-muted mx-2" v-if="isRepeats && intervals.length" />
			<div class="text-right">
				<div v-for="(interval, i) in intervals" :key="i">
					{{ intervalStr(interval) }}
				</div>
			</div>
			<BIcon :icon="`${!$isRtl ? 'chevron-right' : 'chevron-left'}`" class="ml-3" width="1rem" />
		</div>
	</b-list-group-item>
</template>

<script>
	export default {
		props: {
			date: {},
			dateFormat: { type: String, default: "" },
			availableDates: { type: Array, required: true },
		},

		data() {
			return {
				isRepeats: false,
			};
		},

		computed: {
			displayDate() {
				if (this.dateFormat) {
					return this.$moment(this.date).format(this.dateFormat);
				} else {
					if (this.$isRtl) {
						return "ال" + this.$moment(this.date).locale("ar").format("ddd, D MMM");
					} else {
						return this.$moment(this.date).format("ddd, MMM D");
					}
				}
			},

			intervals() {
				let intervals = [];

				for (let availableDate of this.availableDates) {
					let { duration, dates, rruleSet } = availableDate;

					let rruleStr = rruleSet.toString();

					for (let date of dates) {
						date = this.$moment(date).utc();

						if (date.isSame(this.date, "date")) {
							let start = date.hour() + date.minute() / 60;

							let end = start + duration / 60;

							intervals.push({ start, end });

							this.isRepeats = rruleStr.indexOf("BYDAY") != -1;
						}
					}
				}

				return intervals;
			},
		},

		methods: {
			intervalStr({ start, end }) {
				let intervalStart = this.$moment().set(this.hourAndMinute(start)).format("h:mm a");

				let intervalEnd = this.$moment().set(this.hourAndMinute(end)).format("h:mm a");

				return `${intervalStart} - ${intervalEnd}`;
			},

			hourAndMinute(num) {
				let hour = Math.floor(num);
				let minute = Math.floor((num - hour) * 60);

				return { hour, minute };
			},

			clicked() {
				this.$emit("click", { intervals: this.intervals, date: this.date });
			},
		},
	};
</script>
