<template>
	<b-modal id="AvailabilityModal" dialog-class="full_modal no_static_height" hide-footer hide-header centered size="lg" @hidden="reset" @shown="shown">
		<template #default>
			<div class="header d-flex justify-content-between align-items-center">
				<div class="fs-lg">
					<span>{{ $t("availability") }}</span
					>&nbsp;
					<span class="text-muted">{{ displayDate }}</span>
				</div>
				<BIcon icon="x" @click="reset" scale="0.8" class="btn_icon btn_icon_md text-muted" />
			</div>
			<b-form @submit.prevent="save">
				<div class="body mb-3 my-5">
					<div v-if="intervals.length == 0" class="p-4 text-center border rounded-lg text-muted">{{ $t("noAvailability") }}</div>
					<div class="mb-4" v-for="(interval, i) in intervals" :key="i">
						<div class="d-flex align-items-center">
							<form-select
								:label="$t('start')"
								v-model="interval.start"
								:options="intervalsStartOptions"
								class="mr-4"
								:is_invalid="!!error(i)"
								@change="(value) => timeChanged(value, i)"
							></form-select>
							<form-select
								:label="$t('end')"
								v-model="interval.end"
								:options="intervalsEndOptions"
								:is_invalid="!!error(i)"
								@change="(value) => timeChanged(value, i)"
							></form-select>
							<div class="w-25 position-relative" style="top: 12px">
								<span class="btn_icon btn_icon_md mx-auto" @click="removeIntreval(i)">
									<BIcon icon="trash" scale="1.4" class="text-muted" />
								</span>
							</div>
						</div>
						<span class="fs-xs text-danger" v-if="error(i)">{{ error(i).msg }}</span>
					</div>
					<div class="d-flex align-items-center my-4">
						<b-btn variant="outline-primary" class="fs-sm font-weight-500" @click="addInterval">{{ $t("addNewInterval") }}</b-btn>
						<b-btn variant="outline-danger" class="fs-sm font-weight-500 ml-3" :disabled="intervals.length == 0" @click="removeIntrevals">{{ $t("removeAll") }}</b-btn>
					</div>
					<form-select :label="$t('repeats')" v-model="repeats" :options="repeatsOptions" required v-if="!noRepeat"></form-select>
				</div>
				<div class="footer d-flex align-items-center justify-content-end">
					<b-btn class="filter_btn active mx-3" size="sm" @click="reset">{{ $t("cancel") }}</b-btn>
					<b-btn type="submit" variant="primary">{{ $t("apply") }}</b-btn>
				</div>
			</b-form>
		</template>
	</b-modal>
</template>

<script>
	import FormInput from "@/components/inputs/FormInput";
	import FormSelect from "@/components/inputs/FormSelect";

	import { RRule, RRuleSet } from "rrule";

	export default {
		components: { FormInput, FormSelect },

		props: {
			duration: { type: [Number, String], required: true }, // by minute
			dateFormat: { type: String, default: "" },
			noRepeat: { type: Boolean, default: false },
		},

		data() {
			return {
				date: null,
				intervals: [
					{ start: 4, end: 6.5 },
					{ start: 7, end: 17 },
					{ start: 0.5, end: 3 },
					{ start: 9, end: 12 },
				],
				repeats: "only",
				errors: [],
			};
		},

		// mounted() {
		// 	console.log(RRule.TU);
		// },

		computed: {
			displayDate() {
				if (this.dateFormat) {
					return this.date.clone().format(this.dateFormat);
				} else {
					if (this.$isRtl) {
						return "ال" + this.date.clone().format("ddd, D MMM");
					} else {
						return this.date.clone().format("ddd, MMM D");
					}
				}
			},

			intervalsStartOptions() {
				return this.$store.state.invites.intervalsStartOptions;
			},

			intervalsEndOptions() {
				return this.$store.state.invites.intervalsEndOptions;
			},

			repeatsOptions() {
				let date = this.date.clone().startOf("date");

				let options = [
					{ text: `${date.format("MMM D")} ${this.$t("only")}`, value: "only" },
					{ text: `All ${date.format("dddd")}s`, value: "all" },
				];

				if (this.$isRtl) {
					options[1].text = `${this.$t("all")} ${date.format("dddd")}`;
				}

				if (![5, 6].includes(date.weekday())) {
					options.push({ text: this.$t("allWeekdays"), value: "weekdays" });
				}

				return options;
			},
		},

		methods: {
			addInterval() {
				this.intervals.push({ start: 9, end: 17 });
			},

			removeIntreval(index) {
				this.removeError(index);
				this.intervals.splice(index, 1);
			},

			removeIntrevals() {
				this.intervals = [];
				this.errors = [];
			},

			error(index) {
				return this.errors.find((e) => e.index == index);
			},

			removeError(index) {
				this.errors = this.errors.filter((e) => e.index != index);
			},

			setError(index, msg) {
				this.removeError(index);

				this.errors.push({ index, msg });
			},

			validateInterval(index) {
				let { start, end } = this.intervals[index];

				if (start >= end) {
					this.setError(index, this.$t("startTimeMustBeforeEndTime"));
					return false;
				}

				let intervalInMinutes = (end - start) * 60;

				if (intervalInMinutes < +this.duration) {
					this.setError(index, this.$t("intervalMustAtLeastDuration"));
					return false;
				}

				return this.errors.length == 0;
			},

			timeChanged(_, index) {
				this.removeError(index);

				this.validateInterval(index);
			},

			validate() {
				let valid = true;

				for (let i = 0; i < this.intervals.length; i++) {
					if (!this.validateInterval(i)) {
						valid = false;
						break;
					}
				}

				return valid;
			},

			save() {
				if (!this.validate()) return;

				// merge intervals that are ranges of eachother
				let intervals = [];

				let indexsThatNeedToBeRemoved = [];

				for (let i = 0; i < this.intervals.length; i++) {
					if (indexsThatNeedToBeRemoved.includes(i)) continue;

					let interval = this.intervals[i];

					let rangedIntervals = this.intervals.filter(({ start, end }, index) => {
						if (index == i || indexsThatNeedToBeRemoved.includes(index)) return false;

						if ((start > interval.start && start < interval.end) || (interval.start > start && interval.start < end) || start == interval.start || end == interval.end) {
							indexsThatNeedToBeRemoved.push(index);
							return true;
						}

						return false;
					});

					if (rangedIntervals.length > 0) {
						interval = {
							start: Math.min(...rangedIntervals.map(({ start }) => start), interval.start),
							end: Math.max(...rangedIntervals.map(({ end }) => end), interval.end),
						};
					}

					intervals.push(interval);

					indexsThatNeedToBeRemoved.push(i);
				}

				this.$emit("submit", { date: this.date, intervals, repeats: this.noRepeat ? "all" : this.repeats });

				this.$bvModal.hide("AvailabilityModal");
			},

			show({ date, intervals }) {
				this.date = date;
				this.intervals = intervals;
				this.repeats = "only";

				this.$bvModal.show("AvailabilityModal");
			},

			shown() {},

			reset() {
				this.$bvModal.hide("AvailabilityModal");

				setTimeout(() => {
					this.removeIntrevals();
				}, 300);
			},
		},
	};
</script>

<style>
	#AvailabilityModal {
		padding-left: 0 !important;
	}
</style>
